import { mapState} from 'vuex'
export default {
	data(){
		return{
			isNum:false,
			card_num:''
		}
	},
	computed:{
		...mapState({
			userInfo:state=>state.userInfo,
			sessionKey:state=>state.sessionKey
		})
	},
	onLoad(){
		console.log(this.userInfo)
	},
	methods: {
		myPublish(){
			if(!this.sessionKey){
				return uni.showToast({
					title:'请先授权',
					icon:'none'
				})
			}
			uni.navigateTo({
				url:'../myPublish/myPublish'
			})
		},
		toAbout(){
			uni.navigateTo({
				url:'../about/about'
			})
		},
		isMask(){
			this.isNum=false;
		},
		editNum(){
			this.isNum=true;
		},
		bindStuNum(){
			let that=this;
			if(that.card_num===''){
				return uni.showToast({
					title:'请填写学号',
					icon:'none'
				})
			}
			this.$api.bindNum({
				stuNum:that.card_num,
				sessionKey:that.sessionKey,
				userName:that.userInfo.userName,
				userHead:that.userInfo.userHead
			}).then(res=>{
				if(res.data.code===1){
					that.$store.commit('getstuNum',that.card_num);
					uni.showToast({
						title: res.data.msg
					})
				}
				that.isNum=false;
			})
		}
	}
}
